// 
import { log } from './Log';
import { lang } from './Lang';
import { Colors } from './Colors';
import { DOM } from './DOM';
import { centerPanel } from './CenterPanel';
import { centerButtons } from './CenterButtons';
import { store } from './Store';
import { EVT } from './EVT';
class DeleteWorks {
    constructor(worksSelectors) {
        this.worksSelector = ''; // 
        this.multipleSelector = ''; // 
        this.ugoiraSelector = ''; // 
        this.delMode = false; // 
        this.deleteWorkCallback = () => { }; //  callback 
        this.worksSelector = worksSelectors;
        // 
        window.addEventListener(EVT.events.worksUpdate, () => {
            if (this.delMode) {
                this.bindDeleteEvent();
            }
        });
    }
    allowWork() {
        return store.states.allowWork;
    }
    // 
    addClearMultipleBtn(selector, callback = () => { }) {
        this.multipleSelector = selector;
        centerButtons
            .add(Colors.red, lang.transl('_'), [
            ['title', lang.transl('_Title')]
        ])
            .addEventListener('click', () => {
            if (!this.allowWork()) {
                return alert(lang.transl('_'));
            }
            centerPanel.close();
            this.clearMultiple();
            callback();
        }, false);
    }
    // 
    addClearUgoiraBtn(selector, callback = () => { }) {
        this.ugoiraSelector = selector;
        centerButtons
            .add(Colors.red, lang.transl('_'), [
            ['title', lang.transl('_Title')]
        ])
            .addEventListener('click', () => {
            if (!this.allowWork()) {
                return alert(lang.transl('_'));
            }
            centerPanel.close();
            this.ClearUgoira();
            callback();
        }, false);
    }
    // 
    addManuallyDeleteBtn(callback = () => { }) {
        this.deleteWorkCallback = callback;
        const delBtn = centerButtons.add(Colors.red, lang.transl('_'), [
            ['title', lang.transl('_Title')]
        ]);
        delBtn.addEventListener('click', () => {
            this.manuallyDelete(delBtn);
        });
    }
    // 
    clearMultiple() {
        const allPicArea = document.querySelectorAll(this.worksSelector);
        allPicArea.forEach(el => {
            if (el.querySelector(this.multipleSelector)) {
                el.remove();
            }
        });
        this.showWorksCount();
    }
    // 
    ClearUgoira() {
        const allPicArea = document.querySelectorAll(this.worksSelector);
        allPicArea.forEach(el => {
            if (el.querySelector(this.ugoiraSelector)) {
                el.remove();
            }
        });
        this.showWorksCount();
    }
    // 
    bindDeleteEvent() {
        const listElement = document.querySelectorAll(this.worksSelector);
        listElement.forEach(el => {
            el.onclick = ev => {
                if (this.delMode) {
                    ev.preventDefault();
                    if (!this.allowWork()) {
                        return alert(lang.transl('_'));
                    }
                    const target = ev.currentTarget;
                    DOM.removeEl(target);
                    this.showWorksCount();
                    this.deleteWorkCallback(target);
                }
            };
        });
    }
    // 
    // 
    manuallyDelete(delBtn) {
        this.delMode = !this.delMode;
        this.bindDeleteEvent();
        if (this.delMode) {
            delBtn.textContent = lang.transl('_');
            setTimeout(() => {
                centerPanel.close();
            }, 300);
        }
        else {
            delBtn.textContent = lang.transl('_');
        }
    }
    // 
    showWorksCount() {
        const selector = this.worksSelector;
        log.success(lang.transl('_', DOM.getVisibleEl(selector).length.toString()), 2, false);
    }
}
export { DeleteWorks };
