// 
import { InitPageBase } from './InitPageBase';
import { Colors } from './Colors';
import { lang } from './Lang';
import { centerButtons } from './CenterButtons';
import { options } from './Options';
import { API } from './API';
import { store } from './Store';
import { log } from './Log';
import { DOM } from './DOM';
import { pageInfo } from './PageInfo';
class InitUserPage extends InitPageBase {
    constructor() {
        super();
        this.tag = ''; //  tag tag
        this.listType = 0; // 
        this.onceNumber = 48; // 
        this.requsetNumber = 0; // 
        this.offset = 0; // 
        this.init();
    }
    appendCenterBtns() {
        centerButtons
            .add(Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')]
        ])
            .addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_checkWantPageRule1Arg8'),
            rangTip: lang.transl('_1'),
            value: '-1'
        });
        options.hideOption([15, 18]);
    }
    destroy() { }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInput(lang.transl('_checkWantPageRule1Arg6'), lang.transl('_checkWantPageRule1Arg7'));
    }
    nextStep() {
        this.readyGetIdList();
    }
    readyGetIdList() {
        // 
        const nowPage = API.getURLField(location.href, 'p'); //  1 
        if (nowPage) {
            this.offset = (parseInt(nowPage) - 1) * this.onceNumber;
        }
        if (this.offset < 0) {
            this.offset = 0;
        }
        // 
        this.requsetNumber = 0;
        if (this.crawlNumber === -1) {
            this.requsetNumber = 9999999;
        }
        else {
            this.requsetNumber = this.onceNumber * this.crawlNumber;
        }
        // 
        //  pathname  id 
        const test = location.pathname.match(/\/users\/\d+(\/.+)/);
        if (test === null) {
            // 
            this.listType = 0;
        }
        else if (test.length === 2) {
            const str = test[1]; // id 
            if (str.includes('/artworks')) {
                // 
                this.listType = 0;
            }
            else if (str.includes('/illustrations')) {
                // 
                this.listType = 1;
            }
            else if (str.includes('/manga')) {
                // 
                this.listType = 2;
            }
        }
        this.tag = pageInfo.getPageTag;
        if (!this.tag) {
            this.getIdList();
        }
        else {
            if (this.listType === 1) {
                this.getIdListByTag('illusts');
            }
            else if (this.listType === 2) {
                this.getIdListByTag('manga');
            }
            else if (this.listType === 0) {
                this.getIdListByTag('illustmanga');
            }
        }
        log.log(lang.transl('_'));
    }
    // 
    async getIdList() {
        let type = [];
        // 
        if (this.listType === 0) {
            type = ['illusts', 'manga'];
        }
        else if (this.listType === 1) {
            // 
            type = ['illusts'];
        }
        else if (this.listType === 2) {
            // 
            type = ['manga'];
        }
        let idList = await API.getUserWorksByType(DOM.getUserId(), type);
        //  id 
        let tempList = [];
        tempList = idList.map(id => {
            return parseInt(id);
        });
        // 
        tempList.sort(function (x, y) {
            return x - y;
        });
        idList = tempList.map(id => {
            return id.toString();
        });
        //  tag  API 
        //  offset  id
        idList.splice(idList.length - this.offset, idList.length);
        //  requsetNumber  id
        if (idList.length > this.requsetNumber) {
            idList.splice(0, idList.length - this.requsetNumber);
        }
        // 
        store.idList = store.idList.concat(idList);
        this.getIdListFinished();
    }
    //  tag
    async getIdListByTag(type) {
        let data = await API.getUserWorksByTypeWithTag(DOM.getUserId(), type, this.tag, this.offset, this.requsetNumber);
        data.body.works.forEach(data => store.idList.push(data.id));
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.offset = 0;
        this.tag = '';
        this.listType = 0;
        this.listPageFinished = 0;
    }
    sortResult() {
        //  id id 
        store.result.sort(API.sortByProperty('id'));
    }
}
export { InitUserPage };
