// 
import { EVT } from './EVT';
class PageType {
    constructor() {
        this.type = 0;
        this.type = this.getPageType();
        // 
        window.addEventListener(EVT.events.pageSwitch, () => {
            this.checkPageTypeIsNew();
        });
    }
    // 
    // 
    getPageType() {
        const url = window.location.href;
        let type;
        if (window.location.hostname === 'www.pixiv.net' &&
            (window.location.pathname === '/' || window.location.pathname === '/en/')) {
            type = 0;
        }
        else if (/\/artworks\/\d{1,10}/.test(url)) {
            type = 1;
        }
        else if (/\/users\/\d+/.test(url) && !url.includes('/bookmarks')) {
            type = 2;
        }
        else if (location.pathname === '/bookmark.php' ||
            url.includes('/bookmarks')) {
            type = 4;
        }
        else if (url.includes('/tags/')) {
            type = 5;
        }
        else if (location.pathname === '/ranking_area.php' &&
            location.search !== '') {
            type = 6;
        }
        else if (window.location.pathname === '/ranking.php') {
            type = 7;
        }
        else if (url.includes('https://www.pixivision.net') &&
            url.includes('/a/')) {
            type = 8;
        }
        else if (url.includes('/bookmark_add.php?id=') ||
            url.includes('/bookmark_detail.php?illust_id=')) {
            type = 9;
        }
        else if (url.includes('/bookmark_new_illust.php') ||
            url.includes('/bookmark_new_illust_r18.php')) {
            type = 10;
        }
        else if (window.location.pathname === '/discovery') {
            type = 11;
        }
        else if (url.includes('/new_illust.php') ||
            url.includes('/new_illust_r18.php')) {
            type = 12;
        }
        else {
            // 
            throw new Error('Page type matching failed');
        }
        return type;
    }
    // 
    checkPageTypeIsNew() {
        let newType = this.getPageType();
        if (this.type !== newType) {
            EVT.fire(EVT.events.pageTypeChange, newType);
        }
        // 
        this.type = newType;
    }
}
const pageType = new PageType();
export { pageType };
