// 
import { InitPageBase } from './InitPageBase';
import { API } from './API';
import { Colors } from './Colors';
import { lang } from './Lang';
import { centerButtons } from './CenterButtons';
import { options } from './Options';
import { BookmarksAddTag } from './BookmarksAddTag';
import { store } from './Store';
import { log } from './Log';
import { DOM } from './DOM';
import { pageInfo } from './PageInfo';
class InitBookmarkPage extends InitPageBase {
    constructor() {
        super();
        this.idList = []; //  id
        this.tag = ''; //  tag
        this.isHide = false; // 
        this.requsetNumber = 0; // 
        this.onceRequest = 100; // 
        this.offset = 0; // 
        this.crawlRecommended = false; // 
        this.init();
    }
    appendCenterBtns() {
        centerButtons
            .add(Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')]
        ])
            .addEventListener('click', () => {
            this.readyCrawl();
        });
        // 
        const isOldPage = !!document.querySelector('.user-name');
        if (isOldPage) {
            const downRecmdBtn = centerButtons.add(Colors.blue, lang.transl('_'), [['title', lang.transl('_Title')]]);
            downRecmdBtn.addEventListener('click', () => {
                this.crawlRecommended = true;
                this.readyCrawl();
            }, false);
        }
        //  token tag
        if (API.getToken()) {
            const btn = centerButtons.add(Colors.green, lang.transl('_tag'), [
                ['title', lang.transl('_tag')]
            ]);
            new BookmarksAddTag(btn);
        }
    }
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_checkWantPageRule1Arg8'),
            rangTip: lang.transl('_1'),
            value: '-1'
        });
        // 
        options.hideOption([6, 15, 18]);
        if (location.href.includes('bookmark.php')) {
            options.hideOption([6]);
        }
    }
    destroy() { }
    getWantPage() {
        let pageTip = '';
        if (this.crawlRecommended) {
            pageTip = lang.transl('_checkWantPageRule1Arg11');
        }
        else {
            pageTip = lang.transl('_checkWantPageRule1Arg7');
        }
        this.crawlNumber = this.checkWantPageInput(lang.transl('_checkWantPageRule1Arg6'), pageTip);
    }
    nextStep() {
        if (this.crawlRecommended) {
            // 
            this.getRecommendedList();
        }
        else {
            this.readyGetIdList();
        }
    }
    readyGetIdList() {
        // 
        //  20  48  48 
        const isOldPage = !!document.querySelector('.user-name');
        const onceNumber = isOldPage ? 20 : 48;
        // 
        const nowPage = API.getURLField(location.href, 'p'); //  1 
        if (nowPage) {
            this.offset = (parseInt(nowPage) - 1) * onceNumber;
        }
        if (this.offset < 0) {
            this.offset = 0;
        }
        // 
        this.requsetNumber = 0;
        if (this.crawlNumber === -1) {
            this.requsetNumber = 9999999;
        }
        else {
            this.requsetNumber = onceNumber * this.crawlNumber;
        }
        this.tag = pageInfo.getPageTag;
        // 
        //  url rest 
        this.isHide = API.getURLField(location.href, 'rest') === 'hide';
        //  id 
        this.getIdList();
        log.log(lang.transl('_'));
        if (this.crawlNumber === -1) {
            log.log(lang.transl('_'));
        }
    }
    // 
    async getIdList() {
        let data;
        try {
            data = await API.getBookmarkData(DOM.getUserId(), this.tag, this.offset, this.isHide);
        }
        catch (error) {
            this.getIdList();
            return;
        }
        if (data.body.works.length === 0 ||
            this.idList.length >= this.requsetNumber) {
            // 
            return this.afterGetIdList();
        }
        else {
            // 
            data.body.works.forEach(data => this.idList.push(data.id));
            this.offset += this.onceRequest; // 
            // 
            this.getIdList();
        }
    }
    //  id 
    afterGetIdList() {
        //  100 
        if (this.idList.length > this.requsetNumber) {
            // 
            this.idList.splice(this.requsetNumber, this.idList.length);
            //  api  id 
        }
        store.idList = store.idList.concat(this.idList);
        this.getIdListFinished();
    }
    // 
    getRecommendedList() {
        // 
        const elements = document.querySelectorAll('#illust-recommend .image-item');
        if (elements.length === 0) {
            this.crawlRecommended = false;
            return this.noResult();
        }
        // 
        for (const li of elements) {
            const a = li.querySelector('a');
            store.idList.push(API.getIllustId(a.href));
        }
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.idList = [];
        this.offset = 0;
        this.tag = '';
        this.listPageFinished = 0;
        this.crawlRecommended = false; // 
    }
    sortResult() {
        // 
        store.result.reverse();
    }
}
export { InitBookmarkPage };
