// DOM 
//  DOM  DOM  API
class DOM {
    // 
    static getVisibleEl(selector) {
        const list = document.querySelectorAll(selector);
        return Array.from(list).filter(el => {
            return el.style.display !== 'none';
        });
    }
    //  DOM 
    static removeEl(el) {
        if (!el) {
            return;
        }
        if (Reflect.has(el, 'length')) {
            //  length 
            ;
            el.forEach(el => {
                if (el.parentNode) {
                    el.parentNode.removeChild(el);
                }
            });
        }
        else {
            //  length querySelector  HTMLElement
            const parent = el.parentNode;
            if (parent) {
                parent.removeChild(el);
            }
        }
    }
    //  DOM 
    static toggleEl(el) {
        el.style.display = el.style.display === 'block' ? 'none' : 'block';
    }
    // 
    /*
    newindex-inner 
    layout-body 
    */
    static insertToHead(el) {
        if (document.body) {
            document.body.insertAdjacentElement('afterbegin', el);
        }
        else {
            ;
            (document.querySelector('.newindex-inner') ||
                document.querySelector('.layout-body')).insertAdjacentElement('beforebegin', el);
        }
        return el;
    }
    //  id
    static getUserId() {
        const newRegExp = /\/users\/(\d+)/; //  url  id
        //  /users/ 
        //  url 
        const test4 = newRegExp.exec(location.pathname);
        if (!!test4 && test4.length > 1 && !!test4[1]) {
            return test4[1];
        }
        //  id 
        const testA = document.querySelector('.jkOmlQ a') || document.querySelector('a.lknEaI');
        if (testA && testA.href) {
            const test5 = newRegExp.exec(testA.href);
            if (!!test5 && test5.length > 1 && !!test5[1]) {
                return test5[1];
            }
        }
        // 
        const nameElement = document.querySelector('.user-name');
        if (nameElement) {
            return newRegExp.exec(nameElement.href)[1];
        }
        //  body  id
        let test3 = newRegExp.exec(document.body.innerHTML);
        if (test3) {
            return test3[1];
        }
        // 
        throw new Error('getUserId failed!');
    }
}
export { DOM };
