// 
import { lang } from './Lang';
import { Colors } from './Colors';
import { centerButtons } from './CenterButtons';
import { options } from './Options';
import { filter } from './Filter';
import { API } from './API';
import { store } from './Store';
import { log } from './Log';
import { EVT } from './EVT';
import { form } from './Settings';
import { titleBar } from './TitleBar';
import { pageInfo } from './PageInfo';
class InitPageBase {
    constructor() {
        this.crawlNumber = 0; // /
        this.multipleImageWorks = 0; // 
        this.firstFewImages = 0; // 01p
        this.maxCount = 1000; // /
        this.startpageNo = 1; //  api 
        this.listPageFinished = 0; // 
        this.ajaxThreadsDefault = 10; // 
        this.ajaxThreads = this.ajaxThreadsDefault; // 
        this.ajaxThreadsFinished = 0; //  ajaxThreads 
    }
    // 
    init() {
        options.showAllOption();
        this.setFormOption();
        this.appendCenterBtns();
        this.appendElseEl();
        this.initElse();
        window.addEventListener(EVT.events.destroy, () => {
            this.destroy();
        });
    }
    // 
    initElse() { }
    // 
    // CenterButtons 
    destroy() { }
    // 
    appendCenterBtns() {
        centerButtons
            .add(Colors.blue, lang.transl('_'), [
            ['title', lang.transl('_') + lang.transl('_')]
        ])
            .addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    // 
    appendElseEl() { }
    // 
    setFormOption() {
        // /
        options.setWantPage({
            text: lang.transl('_'),
            tip: lang.transl('_checkWantPageRule1Arg8'),
            rangTip: lang.transl('_1'),
            value: '1'
        });
        options.hideOption([15, 18]);
    }
    // /
    getWantPageError() {
        EVT.fire(EVT.events.crawlError);
        const msg = lang.transl('_');
        window.alert(msg);
        throw new Error(msg);
    }
    // /
    //  -1 0
    checkWantPageInput(crawlPartTip, crawlAllTip) {
        const temp = parseInt(form.setWantPage.value);
        //  1  -1
        if ((temp < 1 && temp !== -1) || isNaN(temp)) {
            //  1  -1 , 0 
            this.getWantPageError();
        }
        if (temp >= 1) {
            log.warning(crawlPartTip.replace('-num-', temp.toString()));
        }
        else if (temp === -1) {
            log.warning(crawlAllTip);
        }
        return temp;
    }
    // /
    //  0
    checkWantPageInputGreater0() {
        const result = API.checkNumberGreater0(form.setWantPage.value);
        if (result.result) {
            return result.value;
        }
        else {
            this.getWantPageError();
        }
    }
    // 
    getFirstFewImages() {
        const check = API.checkNumberGreater0(form.firstFewImages.value);
        if (check.result) {
            return check.value;
        }
        else {
            EVT.fire(EVT.events.crawlError);
            const msg = lang.transl('_') + ' ' + lang.transl('_0');
            log.error(msg);
            window.alert(msg);
            throw new Error(msg);
        }
    }
    // 
    getWantPage() { }
    checkNotAllowPage() {
        if (location.href.includes('novel')) {
            EVT.fire(EVT.events.crawlError);
            window.alert('Not support novel page!');
            throw new Error('Not support novel page!');
        }
        // if () {
        //   EVT.fire(EVT.events.crawlError)
        //   window.alert('Not support page!')
        //   throw new Error('Not support page!')
        // }
    }
    //  filter
    getMultipleSetting() {
        this.multipleImageWorks = parseInt(form.multipleImageWorks.value);
        if (this.multipleImageWorks === -1) {
            log.warning(lang.transl('_'));
        }
        // 
        if (this.multipleImageWorks === 1) {
            this.firstFewImages = this.getFirstFewImages();
            log.warning(lang.transl('_n', this.firstFewImages.toString()));
        }
    }
    // 
    async readyCrawl() {
        // 
        this.checkNotAllowPage();
        if (!store.states.allowWork) {
            window.alert(lang.transl('_2'));
            return;
        }
        EVT.fire(EVT.events.crawlStart);
        log.clear();
        log.success(lang.transl('_0'));
        titleBar.change('');
        this.getWantPage();
        filter.init();
        this.getMultipleSetting();
        await pageInfo.store();
        // 
        this.nextStep();
    }
    // 
    nextStep() {
        this.getIdList();
    }
    // 
    getIdListFinished() {
        // 
        this.resetGetIdListStatus();
        if (store.idList.length === 0) {
            return this.noResult();
        }
        log.log(lang.transl('_', store.idList.length.toString()));
        if (store.idList.length <= this.ajaxThreadsDefault) {
            this.ajaxThreads = store.idList.length;
        }
        else {
            this.ajaxThreads = this.ajaxThreadsDefault;
        }
        for (let i = 0; i < this.ajaxThreads; i++) {
            this.getWorksData();
        }
    }
    getPNo(pageCount) {
        let pNo = pageCount;
        if (this.multipleImageWorks === 1 && this.firstFewImages <= pNo) {
            pNo = this.firstFewImages;
        }
        return pNo;
    }
    // 
    //  id
    async getWorksData(id) {
        id = id || store.idList.shift();
        let data;
        try {
            // 
            data = await API.getWorksData(id);
        }
        catch (error) {
            //   response.ok 
            if (error.status) {
                this.logErrorStatus(error.status, id);
                this.afterGetWorksData();
            }
            else {
                // 
                setTimeout(() => {
                    this.getWorksData(id);
                }, 2000);
            }
            return;
        }
        // 
        const body = data.body;
        const fullWidth = body.width; // 
        const fullHeight = body.height; // 
        const bmk = body.bookmarkCount; // 
        const tagArr = body.tags.tags; //  tag 
        const tags = []; //  tag 
        const tagTranslation = []; //  tag  tag
        for (const tagData of tagArr) {
            tags.push(tagData.tag);
            tagTranslation.push(tagData.tag);
            if (tagData.translation && tagData.translation.en) {
                tagTranslation.push(tagData.translation.en);
            }
        }
        const filterOpt = {
            createDate: body.createDate,
            id: body.illustId,
            illustType: body.illustType,
            tags: tags,
            pageCount: body.pageCount,
            bookmarkCount: bmk,
            bookmarkData: body.bookmarkData,
            width: fullWidth,
            height: fullHeight
        };
        // 
        if (filter.check(filterOpt)) {
            const illustId = body.illustId;
            const idNum = parseInt(body.illustId);
            const title = body.illustTitle; // 
            const userid = body.userId; // id
            const user = body.userName; // 
            const thumb = body.urls.thumb;
            const pageCount = body.pageCount;
            const bookmarked = !!body.bookmarkData;
            //  "2019-12-18T22:23:37+00:00"
            //  "2019-12-19" "2019-12-18"
            const date0 = new Date(body.createDate);
            const y = date0.getFullYear();
            const m = (date0.getMonth() + 1).toString().padStart(2, '0');
            const d = date0
                .getDate()
                .toString()
                .padStart(2, '0');
            const date = `${y}-${m}-${d}`;
            let rank = ''; // 
            let testRank = store.getRankList(body.illustId);
            if (testRank !== undefined) {
                rank = '#' + testRank;
            }
            // 
            if (body.illustType !== 2) {
                // 
                // 
                const pNo = this.getPNo(body.pageCount);
                const imgUrl = body.urls.original; //  URL
                const tempExt = imgUrl.split('.');
                const ext = tempExt[tempExt.length - 1];
                // 
                store.addResult({
                    id: illustId,
                    idNum: idNum,
                    thumb: thumb,
                    pageCount: pageCount,
                    url: imgUrl,
                    title: title,
                    tags: tags,
                    tagsTranslated: tagTranslation,
                    user: user,
                    userid: userid,
                    fullWidth: fullWidth,
                    fullHeight: fullHeight,
                    ext: ext,
                    bmk: bmk,
                    bookmarked: bookmarked,
                    date: date,
                    type: body.illustType,
                    rank: rank
                }, pNo);
                this.logImagesNo();
            }
            else if (body.illustType === 2) {
                // 
                // 
                const meta = await API.getUgoiraMeta(illustId);
                // 
                const ugoiraInfo = {
                    frames: meta.body.frames,
                    mime_type: meta.body.mime_type
                };
                const ext = form.ugoiraSaveAs.value; //  webmgifzip
                store.addResult({
                    id: illustId,
                    idNum: idNum,
                    thumb: thumb,
                    pageCount: pageCount,
                    url: meta.body.originalSrc,
                    title: title,
                    tags: tags,
                    tagsTranslated: tagTranslation,
                    user: user,
                    userid: userid,
                    fullWidth: fullWidth,
                    fullHeight: fullHeight,
                    ext: ext,
                    bmk: bmk,
                    bookmarked: bookmarked,
                    date: date,
                    type: body.illustType,
                    rank: rank,
                    ugoiraInfo: ugoiraInfo
                });
                this.logImagesNo();
            }
        }
        this.afterGetWorksData();
    }
    // 
    afterGetWorksData() {
        if (store.idList.length > 0) {
            // 
            this.getWorksData();
        }
        else {
            // 
            this.ajaxThreadsFinished++;
            if (this.ajaxThreadsFinished === this.ajaxThreads) {
                // 
                this.ajaxThreadsFinished = 0;
                this.crawlFinished();
            }
        }
    }
    // 
    crawlFinished() {
        if (store.result.length === 0) {
            return this.noResult();
        }
        this.sortResult();
        log.log(lang.transl('_', store.result.length.toString()));
        log.log(lang.transl('_'), 2);
        EVT.fire(EVT.events.crawlFinish);
    }
    // 
    logErrorStatus(status, id) {
        log.error(lang.transl('_2', id), 1);
        switch (status) {
            case 0:
                console.log(lang.transl('_0'));
                break;
            case 400:
                console.log(lang.transl('_400'));
                break;
            case 403:
                console.log(lang.transl('_403'));
                break;
            case 404:
                console.log(lang.transl('_404') + ' ' + id);
                break;
            default:
                break;
        }
    }
    // 
    logImagesNo() {
        log.log(lang.transl('_', store.result.length.toString()), 1, false);
    }
    //  0 
    noResult() {
        EVT.fire(EVT.events.crawlEmpty);
        titleBar.reset();
        log.error(lang.transl('_'), 2);
        window.alert(lang.transl('_'));
    }
    // 
    sortResult() { }
}
export { InitPageBase };
